.navbar-default {
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #ff00dd), to(#000000));
    background-image: -webkit-linear-gradient(top, #ff00dd 0, #000000 100%);
    background-image: -o-linear-gradient(top, #ff00dd 0, #000000 100%);
    background-image: linear-gradient(to bottom, #ff00dd 0, #000000 100%);
    border-color: #000000;
    font-family: 'Coming Soon', cursive;
    font-size: 150%
}

.navbar-default .navbar-text {
    color: #fff;
}


